import React, {Component} from 'react';
import Dropzone from 'react-dropzone';
import "./imageUpload.css";

class ImageUpload extends Component {
  constructor() {
    super();
    this.state = {
      files: []
    };
  }

  onDrop = (files) => {
    files.map(file => Object.assign(file, {
      preview: URL.createObjectURL(file)
    }));
    this.setState({files}, () => {
      this.props.onSendUploadedFiles(this.state.files);
    })
  };

  render() {
    const maxSize = 20971520; /* 20 MB */
    const files = this.state.files.map(file => (
      <li key={file.name}>
        <img alt="" src={file.preview} width="50"/>
      </li>
    ));

    return (
      <>
      <Dropzone
        onDrop={this.onDrop}
        accept="image/*"
        multiple={true}
        minSize={0}
        maxSize={maxSize}>
        {({getRootProps, getInputProps, isDragReject, rejectedFiles}) => {
        const isFileTooLarge = rejectedFiles.length > 0 && rejectedFiles[0].size > maxSize;
        return (
          <section className="container main-border mb-4">
            <div {...getRootProps({className: 'dropzone'})}>
              <input {...getInputProps()} />
              {isDragReject && <p>File type not accepted, sorry!</p>}
              {isFileTooLarge && (
                <div className="text-danger text-center mt-2">
                  File is too large. Up to 20MB file size allowed.
                </div>
              )}
              <p>Drag 'n' drop some files here, or click to select files</p>
            </div>
            <aside >
              <h4>Files</h4>
              <ul>{files}</ul>
            </aside>
          </section>
        )}
        }
      </Dropzone>
      </>
    );
  }
}

export default ImageUpload;